import React from "react";
import {NavLink} from "react-router-dom";

const Header = () => {
    return(
        <div className="container-fluid">
            <nav className="navbar navbar-expand-lg" style={{backgroundColor: "wheat"}}>
                <NavLink to="/" className="btn" style={{ color: "white"}}>
                    Home
                </NavLink>

                

                <NavLink to="/list" className="btn" style={{ color: "white"}}>
                    List
                </NavLink>
                <NavLink to="/details" className="btn" style={{ color: "white"}}>
                    Details
                </NavLink>


            </nav>
        </div>
    )
}

export default Header